* {
  box-sizing: border-box;
}

body {
  color: #dcddde;
  background-color: #36393f;
  font-family: Whitney, sans-serif;
}

button {
  cursor: pointer;
}

section {
  text-align: center;
  padding: 100px 0;
}

section.calc {
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.container {
  width: 1440px;
  margin: 0 auto;
  padding: 40px 0;
}

button.modal-open, button.calc-btn__show {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #43b581;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button.modal-open:hover, button.calc-btn__show:hover {
  background-color: #3fb072;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s linear, visibility .5s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal {
  width: 96vw;
  max-width: 450px;
  height: 100vh;
  max-height: 610px;
  background-color: #fff;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
}

.close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 8px;
}

@media screen and (min-width: 1200px) {
  .modal {
    max-width: 528px;
    max-height: 580px;
  }
}

.login-form {
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  display: flex;
}

.login-form label {
  margin-bottom: 16px;
}

.login-form input, .login-form button {
  width: 100%;
  font: inherit;
  padding: 4px;
}

.calculator {
  width: 270px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 50px auto;
  padding: 10px;
}

.calculator-display {
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 5px;
}

.calculator-display input[type="text"] {
  width: 100%;
  text-align: right;
  border: none;
  outline: none;
  font-size: 30px;
}

.calculator-row {
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
}

.calculator-button {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #e0e0e0;
  border: none;
  border-radius: 5px;
  outline: none;
  font-size: 24px;
}

.calculator-button-orange {
  color: #fff;
  background-color: #ff8c00;
}

.calculator-button-orange:hover, .calculator-button-orange:focus {
  color: #ff8c00;
  background-color: #fff;
}

.calculator-button-gray {
  color: #000;
  background-color: #e0e0e0;
}

.calculator-button-double {
  width: 110px;
}

.calculator-button:active {
  background-color: #d3d3d3;
}

.todos-form {
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.todos-input {
  color: #dcddde;
  height: 36px;
  background-color: #40444b;
  border: none;
  border-radius: 5px;
  flex-grow: 1;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #7289da;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="submit"]:hover {
  background-color: #677bc4;
}

li {
  background-color: #40444b;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  padding: 8px;
  display: flex;
}

li:last-child {
  margin-bottom: 0;
}

button.delete, button.calc-close {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #f04747;
  border: none;
  border-radius: 5px;
  margin-right: 8px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button.delete:hover, button.calc-close:hover {
  background-color: #d03c3c;
}

span.done {
  color: red;
  font-style: italic;
  text-decoration: line-through;
}

